<script>
export default {
  props: {
    videoUrl: {
      type: String,
      default: null
    },
    merchantId: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      show: true
    }
  },
  methods: {
    hideModal() {
      this.$emit('hideVideo')
      this.show = !this.show
    },
    shareVideo() {
      this.$axios.post('https://beta.wxj360.com/app/index.php?i=2&c=entry&m=crab_hot_video&do=douyin&op=index&&fn=upload_video&activity=4', {
        merchant_id: this.merchant_id
