<template>
  <div class="templateWrapper" :style="offsetBottom">
    <img src="~@/assets/images/backgrount_top.gif" />
    <img src="~@/assets/images/backgrount_top_center.png" />
    <img src="~@/assets/images/backgrount_bottom_center.png" />
    <img src="~@/assets/images/backgrount_bottom.png" />
    <div ref="shareButton" class="shareButton" @click="showVideo">
      <img src="~@/assets/images/button.png" />
    </div>

    <!-- 弹出转发视频  -->
    <Modal
      v-if="showModal"
      :video-url="videoInfo.video_url"
      :merchant-id="videoInfo.merchant"
      @hideVideo="hideVideo"
    ></Modal>
  </div>
</template>

<script>
import Modal from './components/videoModal'
export default {
  name: 'Restaurant',
  components: {
    Modal
  },
  data() {
    return {
      offsetBottom: {
        paddingBottom: null + 'px'
      },
      showModal: false,
      videoInfo: {},
      urlInfo: {
        url: '',
        i: '',
        ay: ''
      }
    }
  },
  mounted() {
    setTimeout(() => {
      this.getShareButtonHeight()
    }, 50)
    this.getVideoInfo()
    console.group('url params:')
    const url = window.location.href
    const i = url.includes('i=')
    const ay = url.indexOf('ay=')
    this.urlInfo.i = i
    this.urlInfo.ay = ay
    console.log(url)
    console.groupEnd()
  },
  methods: {
    getShareButtonHeight() {
      const buttonHeight = this.$refs.shareButton.clientHeight
      this.offsetBottom.paddingBottom = buttonHeight - 16 + 'px'
      console.log(this.offsetBottom.paddingBottom)
    },

    showVideo() {
      this.showModal = true
    },

    hideVideo() {
      this.showModal = false
    },
    getVideoInfo() {
      this.$axios
        .get(
          'https://beta.wxj360.com/app/index.php?i=2&c=entry&m=crab_hot_video&do=douyin&op=index&&fn=index_ajax&ay=4'
        )
        .then(res => {
          console.group('视频信息')
          // console.log(res)
          this.videoInfo = res.data
          console.log(this.videoInfo)
          console.groupEnd()
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.shareButton {
  position: fixed;
  bottom: 0;
}
</style>
